
import Confirm from 'vux/src/components/confirm/index.vue'


export default {
components: {
Confirm
},
props: ['isShow'],
data () {
return {
title: '驳回理由',
show: this.isShow,
content: '',
}
},
methods: {
reject: function() {
this.$emit('close');
},
onCancel: function() {
this.$emit('close');
},
onShow: function() {

},
},
watch: {
isShow () {
this.show = this.isShow
}
}
}



